
import { Component, OnInit, HostListener, ViewChild, Inject ,OnDestroy} from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AgentService } from '../../shared/services/agent';

//Mat table 
import { MatTableDataSource } from '@angular/material';
import {MatPaginator} from '@angular/material/paginator';
import {SelectionModel} from '@angular/cdk/collections';
import {MatSort} from '@angular/material/sort';

@Component({
  selector: 'app-popup-parent-calls',
  templateUrl: './popup-parent-calls.component.html',
  styleUrls: ['./popup-parent-calls.component.css']
})
export class PopupParentCallsComponent implements OnInit {
  selection = new SelectionModel(true, []);
  dataSource1: MatTableDataSource<any>;
  dataSource2: MatTableDataSource<any>;

  
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  displayedColumns1 = [ 'S No', 'customerName',  'customerMobNo',  'createdAt', 'descrptionOnCall']; 

  displayedColumns2 = [  'createdAt', 'callBackStartTime', 'callBackEndTime', 'descrptionOnCall']; 

  id;
  boolean= false;
  constructor( private agentService: AgentService,
    public dialogRef: MatDialogRef<PopupParentCallsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      console.log("id:",data.documentId);
      this.id = data.documentId
     }

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit() {
    this.agentService.getCallRelatedRecords(this.id).subscribe((data)=>{
      this.boolean = true;
      console.log(data);
     // this.MyNotes = data['message'];
      this.dataSource1 = new MatTableDataSource(data['message']);
      this.dataSource2 = new MatTableDataSource(data['getChilds']);
      this.dataSource2.paginator = this.paginator;
      this.dataSource2.sort = this.sort;

    },(err) => {
      debugger
      console.log(err)
    });
  }

}
